    background: var(--color-primary-variant);
}
.blur{
    -webkit-filter: blur(20px);
            filter: blur(20px);
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    pointer-events: none;
}
.butn.close{
    margin-top: 1rem;
}
.pop__up{
    background-color: var(--color-bg-variant);
    width: 50%;
    /* min-height: 25rem; */
    text-align: center;
    padding: 1rem;
    border: 5px solid var(--color-white);
    border-radius: 2rem;
    position: fixed;
    top: 20%;
    left: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    z-index: 11;
    visibility: hidden;
}
.pop__up h4{
    color: var(--color-primary);
}
.pop__up.active{
    visibility: visible;
    top: 30%;
    transition: 0.5s;
}
.inactive{
    cursor: default;
    pointer-events: none;
    -webkit-text-decoration-line: line-through;
            text-decoration-line: line-through;
    text-decoration-thickness: 2px;
}
